import React, { useState } from 'react'
import { ITag } from '../../page/tag/interface'
import { fetchData } from '../../util/fetch'
import './index.scss'

interface IProps {
  tagObj: ITag
  updateList: any
}

export const TagItem: React.FC<IProps> = (props) => {

  const [isHover, setIsHover] = useState(false)

  const deleteTag = () => {
    const url = `/api/tag/${props.tagObj.id}`
    fetchData(url, 'delete').then(res => {
      props.updateList()
    })
  }

  return (
    <div className='tagComponentWrapper flexcc' onMouseEnter={() => setIsHover(true)} onMouseLeave={() => setIsHover(false)} >
      <div className='flexRow'>
        <h1>{props.tagObj.name}</h1>
        {isHover && <img onClick={deleteTag} className='delImg' alt='img_demo' src='data:image/png;base64,' />}
      </div>
    </div>
  )
}